import React, { Component } from 'react'
import BottomNavBar from '../Header/BottomNarBar';
import './Bishop.scss';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';

class Bishop extends Component {
    render() {
        return (
            <div>
                 Bishop
                <BottomNavBar/>
            </div>
        )
    }
}

export default connect(null, { ...getAppReduxStateObject() })(withRouter(Bishop));
