import React, { Component } from 'react';
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAppReduxStateObject } from '../../reduxStateMapping';
// import { isNullOrUndefined } from 'util';
import './Login.scss';
import BBMLogo from '../../images/bmm.jpeg';
import * as pageUrls from '../constants/pageUrls';
import * as UserRoles from '../constants/permissions'; 
class LoginForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
          username:'',
          password:'',
          role:0
        }
      }

    componentDidMount(){
       this.props.getAllUsers()
    }

    handleSubmit = () =>{
      let {username, password} = this.state;
      this.props.login(username,password);
      this.checkIsValidUser();
    }

    getUsername = (event) => {
      this.setState({username:event.target.value});
    }

    getPassword = (event) =>{
      this.setState({password:event.target.value});
    }

    componentDidUpdate = () => {
      
    }

    checkIsValidUser = () =>{
      let { isLoggedIn, designation } = this.props;
      console.log('designation.toUpperCase()', designation.toUpperCase())
      if(isLoggedIn){
          switch(designation.toUpperCase() ){

            case UserRoles.DISCIPLER:
                return <Redirect to={pageUrls.DISCIPLER} />
                break;
            case UserRoles.BISHOP:
              return <Redirect to={pageUrls.BISHOP} />
                break;
            case UserRoles.ADMIN:
              return <Redirect to={pageUrls.ADMIN} />
                break;
            default:
               return <Redirect to={pageUrls.DISCIPLER} />
                break;
          }
      }
    }
    
    render() {
        return (
          <div className={'login-container app'}>
                
          <div className="bg"></div>
          <form>
              <header>
                  <img src={BBMLogo}/>
              </header>
              <div className="inputs">
                  <input type="text" name="" onChange={this.getUsername} placeholder="username"/>
                  <input type="password" name=""  onChange={this.getPassword}  placeholder="password"/>

                  <p class="light"><a href="#">Forgot password?</a></p>
              </div>
          </form>
          <footer>
              <button onClick={()=>this.handleSubmit()}>Continue</button>
              <p>Don't have an account? <a href="/">Go back</a></p>
          </footer>
        </div>
      );
    }
  }
  
const mapStateToProps = ({ userReducer={} }) => (
  {
      isLoggedIn: userReducer.isLoggedIn,
      isLoadingData: userReducer.isLoadingData,
      designation: userReducer.designation,
      username: userReducer.username,
      fName: userReducer.fName,
      surname: userReducer.surname,
      province: userReducer.province    
  }
);

export default connect(mapStateToProps, { ...getAppReduxStateObject() })(withRouter(LoginForm));
